/* eslint-disable prettier/prettier */
import { prdStatCd, prdSelCd, prdStatCdArray, prdSelCdArray } from '@/utils/jsUtils/jsConstants.js';
import { formatDate } from '@/utils/jsUtils/filters.js';
import initCssEvent from '@/utils/jsUtils/cssUtils.js';
import { categoryApi } from '@/api/categoryApi.js';
import { codeApi } from '@/api/codeApi.js';

import image256 from '@/assets/img/no_img_256x256.png';
import image64 from '@/assets/img/no_img_64x64.png';

export const mixinUtils = {
    data() {
        return {
            constant : {
                prdStatCd: prdStatCd,
                prdSelCd: prdSelCd,
                prdStatCdArray: prdStatCdArray,
                prdSelCdArray: prdSelCdArray,
            },
            code: {
                ctgr1List : [], // 1 
                ctgr2List : [], // 2 
                ctgr3List : [], // 3 
                nationList : [],
                genderList : [],
                seasonList : [],
            },
        }
    },
    methods: {
        async getNationList() {
            if(this.code.nationList.length == 0) {
                let data = await codeApi.getNationCodeListApi().then(response => {
                    let resultList = response.data;
                    if(resultList.length > 0) {
                        for(let item of resultList) {
                            if(item.useYn == 'Y') {
                                let temp = {
                                    grp: item.cdVal1,
                                    value: item.dtlsCd,
                                    name: item.dtlsCdDesc
                                }
                                this.code.nationList.push(temp);
                            }
                        }
                    }
                    return this.code.nationList;
                });
                return data;
            } else {
                return this.code.nationList;
            }
        }, 
        async getGenderList() {
            if(this.code.genderList.length == 0) {
                let data = await codeApi.getGenderCodeListApi().then(response => {
                    let resultList = response.data;
                    if(resultList.length > 0) {
                        for(let item of resultList) {
                            if(item.useYn == 'Y') {
                                let temp = {
                                    grp: item.grpCd,
                                    value: item.dtlsCd,
                                    name: item.dtlsComNm
                                }
                                this.code.genderList.push(temp);
                            }
                        }
                    }
                    return this.code.genderList;
			    });
                return data;
            } else {
                return this.code.genderList;
            }
        }, 
        async getSeasonList() {
            if(this.code.seasonList.length == 0) {
                let data = await codeApi.getSeasonCodeListApi().then(response => {
                    let resultList = response.data;
                    if(resultList.length > 0) {
                        for(let item of resultList) {
                            if(item.useYn == 'Y') {
                                let temp = {
                                    grp: item.grpCd,
                                    value: item.dtlsCd,
                                    name: item.dtlsComNm
                                }
                                this.code.seasonList.push(temp);
                            }
                        }
                    }
                    return this.code.seasonList;
			    });
                return data;
            } else {
                return this.code.seasonList;
            }
        }, 
        async setCtgrList(depth, hgrnkCtgrNo, siteCd) {
            depth = Number(depth);
            siteCd = siteCd ?? '1';
            hgrnkCtgrNo = depth == 1 ? 0 : hgrnkCtgrNo ?? 0;
            if(this.$webUtils.isNull(hgrnkCtgrNo)) return false;

            const params = {
                siteCd: siteCd,
                hgrnkCtgrNo: hgrnkCtgrNo,
                depth: depth
            }

            return await categoryApi.selectStdCtgrListApi(params).then(response => {
                if(depth == 1) {
                    this.code.ctgr1List = response.data;
                } else if(depth == 2) {
                    this.code.ctgr2List = response.data;
                } else {
                    this.code.ctgr3List = response.data;
                }
                
            }).catch(error => {
                console.log(error);
            });
        },
        getNoImage256(e) {
            e.target.src = image256
        },
        getNoImage64(e) {
            e.target.src = image64
        },
        onDevelopingAlert() {
            alert(".");
        },
        goHome() {
            this.$router.push('/');
        },
        ...initCssEvent
    }
};
