import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '../store/index';

Vue.use(VueRouter);

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes: [
		{
			path: '/',
			name: 'Home',
			component: () => import(/* webpackChunkName: "about" */ '@/common/Home.vue'),
			meta: { pagePath1: '', pagePath2: 'HOME', pagePath3: '' },
		},
		{
			path: '/about',
			name: 'About',
			// route level code-splitting
			// this generates a separate chunk (about.[hash].js) for this route
			// which is lazy-loaded when the route is visited.
			component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
		},
		{
			path: '/productList',
			name: 'ProductList',
			// route level code-splitting
			// this generates a separate chunk (about.[hash].js) for this route
			// which is lazy-loaded when the route is visited.
			component: () =>
				import(/* webpackChunkName: "about" */ '@/components/productManage/ProductList.vue'),
			meta: { pagePath1: '', pagePath2: '', pagePath3: '' },
		},
		{
			path: '/productListOnReg',
			name: 'productListOnReg',
			// route level code-splitting
			// this generates a separate chunk (about.[hash].js) for this route
			// which is lazy-loaded when the route is visited.
			component: () =>
				import(/* webpackChunkName: "about" */ '@/components/productManage/ProductListOnReg.vue'),
			meta: { pagePath1: '', pagePath2: '', pagePath3: ' ' },
		},
		{
			path: '/productDetail/:prdNo',
			name: 'ProductDetail',
			// route level code-splitting
			// this generates a separate chunk (about.[hash].js) for this route
			// which is lazy-loaded when the route is visited.
			component: () =>
				import(/* webpackChunkName: "about" */ '@/components/productRegister/ProductDetail.vue'),
			meta: { pagePath1: '', pagePath2: ``, pagePath3: '' },
		},
		{
			path: '/test',
			name: 'Test',
			// route level code-splitting
			// this generates a separate chunk (about.[hash].js) for this route
			// which is lazy-loaded when the route is visited.
			component: () => import(/* webpackChunkName: "about" */ '../common/Test.vue'),
		},
		//   
		{
			path: '/*',
			name: 'NoPage',
			component: () => import(/* webpackChunkName: "about" */ '../common/NoPage.vue'),
		},
	],
});

router.beforeEach((to, from, next) => {
	// console.log(to); // to      
	if (to.name == 'ProductDetail' && to.params.prdNo != '0') {
		store.state.pagePath1 = '';
		store.state.pagePath2 = '';
		store.state.pagePath3 = '';
	} else {
		store.state.pagePath1 = to.meta.pagePath1;
		store.state.pagePath2 = to.meta.pagePath2;
		store.state.pagePath3 = to.meta.pagePath3 || '';
	}

	// if (to.meta.auth && !store.getters.isLogin) {
	// 	console.log(' ');
	// 	next('/login');
	// 	return;
	// }
	next();
});

export default router;
