export const webUtils = {
	/**
	 * null 
	 * str : 
	 */
	isNull: function (str) {
		var type = typeof str;

		if (type === 'undefined' || str === null) {
			return true;
		}

		if (type === 'string' && this.trim(str) == '') {
			return true;
		}

		return false;
	},

	/**
	 * not null 
	 * str : 
	 */
	isNotNull: function (str) {
		return !this.isNull(str);
	},

	/**
	 * object 
	 * obj : 
	 */
	isObject: function (obj) {
		if (obj !== null && typeof obj === 'object') {
			return true;
		}

		return false;
	},

	/**
	 *  
	 * str : 
	 */
	trim: function (str) {
		if (typeof str === 'string') {
			return str.replace(/^[\s\uFEFF\xA0]+|[\s\uFEFF\xA0]+$/g, '');
		}

		return str;
	},

	/**
	 * null  
	 * str : 
	 * val : null 
	 */
	nvl: function (str, val) {
		if (this.isNull(str)) {
			return val;
		}

		return this.trim(str);
	},

	/**
	 *  
	 * str     : 
	 * pattern : 
	 * re      :  
	 */
	replaceAll: function (str, pattern, re) {
		var type = typeof str;

		if (type === 'string' || type === 'number') {
			var temp = this.trim(str) + '';

			if (temp != '') {
				return temp.replace(new RegExp(pattern, 'g'), re);
			}
		}

		return str;
	},

	/**
	 * prefix 
	 * str   : 
	 * front : 
	 * back  : 
	 */
	prefix: function (str, front, back) {
		var type = typeof str;

		if (type === 'string' || type === 'number') {
			if (this.isNotNull(str)) {
				var result = this.trim(str) + '';

				// 
				if (this.isNotNull(front)) {
					result = front + result;
				}

				// 
				if (this.isNotNull(back)) {
					result = result + back;
				}

				return result;
			}
		}

		return '';
	},

	/**
	 *  integer 
	 * str : , 
	 */
	toInt: function (str) {
		var type = typeof str;

		if (type === 'string' || type === 'number') {
			var temp = this.trim(str) + '';

			if (temp != '' && !isNaN(temp)) {
				return parseInt(temp, 10);
			}
		}

		return 0;
	},

	/**
	 *  float 
	 * str   : , 
	 * point :  
	 * max   : 
	 */
	toFloat: function (str, point, max) {
		var type = typeof str;
		var temp = '';

		if (type === 'string' || type === 'number') {
			temp = this.trim(str) + '';

			if (temp != '' && !isNaN(temp)) {
				var pow = Math.pow(10, point);
				temp = Math.floor(temp * pow) / pow;
				temp = Number(temp.toFixed(point));

				if (temp > max) {
					temp = max;
				}

				return temp;
			}
		}

		return Number((0).toFixed(point));
	},

	/**
	 *  
	 * str : 
	 */
	toNumber: function (str) {
		if (typeof str === 'string') {
			var symbol = '';

			//   
			if (str.indexOf('-') == 0) {
				symbol = '-';
			}

			return symbol + str.replace(/[^0-9]+/g, '');
		}

		return str;
	},
	toOnlyNumber: function (valueStr) {
		var re = /^[0-9\r\n]+$/;
		if (!re.test(valueStr)) {
			//Swal.alert([$.t('common.numinert.chk'), 'warning']);
			valueStr = String(valueStr).replace(/[^0-9]/g, '');
		}
		return valueStr;
	},

	/**
	 *  
	 * str : 
	 */
	toDecimal: function (str) {
		if (typeof str === 'string') {
			var symbol = '';

			//   
			if (str.indexOf('-') == 0) {
				symbol = '-';
			}

			var num = str.replace(/[^0-9\\.]+/g, '');
			var pointCnt = (num.match(/\./g) || []).length;

			if (pointCnt > 1) {
				var tempArray = num.split('.');
				num = tempArray[0] + '.' + tempArray[1];
			}

			return symbol + num;
		}

		return str;
	},

	/**
	 *   
	 * str : , 
	 */
	addThousandComma: function (str) {
		var type = typeof str;

		if (type === 'string' || type === 'number') {
			var temp = this.trim(str) + '';

			if (temp != '') {
				return temp.replace(/\B(?=(\d{3})+(?!\d))/g, ',');
			}
		}

		return str;
	},

	/**
	 * html  
	 * str : 
	 */
	removeTag: function (str) {
		if (typeof str === 'string') {
			return str.replace(/<(\s*\/)?(\s*[a-z]*)(\s[a-z]*=[^>]*)?(\s)*(\/\s*)?>/gi, '');
		}

		return str;
	},

	arranyPush: function (textList) {
		let resultList = [];

		for (let item of textList) {
			if (this.isNotNull(item)) {
				resultList.push(this.trim(item));
			}
		}
		return resultList;
	},

	/**
	 *    
	 * str : 
	 */
	toEnterArray: function (str) {
		let resultList = [];
		if (this.isNotNull(str)) {
			let textList = str.split('\n');
			resultList = this.arranyPush(textList);
		}
		return resultList;
	},

	toSpaceAndEnterArray: function (str) {
		let resultList = [];
		if (this.isNotNull(str)) {
			let tempList = str.split('\n');
			let textList = [];
			for (let item of tempList) {
				let ttt = item.split(' ').reduce(function (acc, elm, idx) {
					elm.trim() != '' ? acc.push(elm) : acc;
					return acc;
				}, []);
				textList = [...textList, ...ttt];
			}
			resultList = this.arranyPush(textList);
		}
		return resultList;
	},

	/**
	 *  
	 */
	arrayCount: function (itemList) {
		var itemCnt = 0;

		if (Array.isArray(itemList)) {
			itemCnt = itemList.length;
		}

		return itemCnt;
	},

	/**
	 *   
	 */
	isArrayDup: function (itemList, key, val) {
		var itemCnt = this.arrayCount(itemList);
		var m = 0;
		var dupYn = false;

		for (m = 0; m < itemCnt; m++) {
			//  
			if (itemList[m][key] == val) {
				dupYn = true;
				break;
			}
		}

		return dupYn;
	},

	/**
	 *  
	 */
	deepCopyObject: function (obj) {
		let clone = {};
		for (let key in obj) {
			if (typeof obj[key] == 'object' && obj[key] != null && Array.isArray(obj[key])) {
				clone[key] = [...obj[key]];
			} else if (typeof obj[key] == 'object' && obj[key] != null) {
				clone[key] = this.deepCopyObject(obj[key]);
			} else {
				clone[key] = obj[key];
			}
		}
		return clone;
	},
	deepCopyArray: function (array) {
		let clone = [];
		for (let item of array) {
			if (typeof item == 'string') {
				clone.push(item);
			} else {
				clone.push(this.deepCopyObject(item));
			}
		}
		return clone;
	},
	makeQueryString: function (params) {
		let queryString = '?';
		for (let el in params) {
			queryString += `${el}=${params[el]}&`;
		}
		queryString = queryString.substring(0, queryString.length - 1);
		return queryString;
	},
	getUniqueId: function () {
		return String(Math.random());
	},
};
