export function formatDate(value) {
	if (value == null || value == '') {
		return '';
	} else {
		const { year, month, day } = setDate(value);
		return `${year}-${month}-${day}`;
	}
}

export function formatDateFull(value) {
	if (value == null || value == '') {
		return '';
	} else {
		const { year, month, day, hours, minutes, seconds } = setDate(value);
		return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
	}
}

export function setDate(value) {
	const date = new Date(value);
	const year = date.getFullYear();
	let month = date.getMonth() + 1;
	month = month > 9 ? month : `0${month}`;
	let day = date.getDate();
	day = day > 9 ? day : `0${day}`;
	let hours = date.getHours();
	hours = hours > 9 ? hours : `0${hours}`;
	let minutes = date.getMinutes();
	minutes = minutes > 9 ? minutes : `0${minutes}`;
	let seconds = date.getSeconds();
	seconds = seconds > 9 ? seconds : `0${seconds}`;
	return { year, month, day, hours, minutes, seconds };
}

export function thousandFormat(value) {
	value = parseFloat(value);
	const numFix = 0; //  
	if (!value) return '0';
	return value.toFixed(numFix).replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ',');
}
