import $ from 'jquery';
// import flowbite from 'flowbite';

// const burger = document.querySelector('#burger');
// const menu = document.querySelector('#menu');

export default function initCssEvent() {
	// $('#burger')
	// 	.off('click')
	// 	.on('click', () => {
	// 		if ($('#menu').hasClass('hidden')) {
	// 			$('#menu').removeClass('hidden');
	// 		} else {
	// 			$('#menu').addClass('hidden');
	// 		}
	// 	});
	$('body')
		.not('svg' || 'path' || '#burger' || '#burgerIcon')
		.off()
		.on('click', e => {
			const tagName = $(e.target).prop('tagName');
			const tagId = $(e.target).prop('tagName');
			if (tagName == 'svg' || tagName == 'path' || tagId == 'burger') {
				if ($('#menu').hasClass('hidden')) {
					$('#menu').removeClass('hidden');
				} else {
					$('#menu').addClass('hidden');
				}
			} else {
				if (!$('#menu').hasClass('hidden')) {
					$('#menu').addClass('hidden');
				}
			}
		});

	$('.nav.metismenu > div')
		.not('.nav-header')
		.off('click')
		.on('click', e => {
			$('.nav.metismenu > div').each((idx, el) => {
				if ($(el).hasClass('active')) {
					$(el).removeClass('active');
					// $(el).find('ul').addClass('collapse');
					$(el).find('ul').addClass('hidden');
				}
			});

			const __this = $(e.currentTarget);
			const isHasUlElement = __this.find('ul').length == 0 ? false : true;
			const ul = __this.find('ul');
			const classNm = 'active';
			if (__this.hasClass(classNm)) {
				__this.removeClass(classNm);
				if (isHasUlElement) {
					// ul.addClass('collapse');
					ul.addClass('hidden');
				}
			} else {
				__this.addClass(classNm);
				if (isHasUlElement) {
					// ul.removeClass('collapse');
					ul.removeClass('hidden');
				}
			}
		});
}
