import axios from 'axios';
import { setLoadingInterceptor } from '@/api/apiConfig/interceptor.js';

export const config = {
	baseUrl: process.env.VUE_APP_API_URL,
};

export const instance = createInstance();

export const instanceAddLoading = setLoadingInterceptor(createInstance());

function createInstance() {
	// axios       create()
	return axios.create({
		baseURL: config.baseUrl,
		headers: { 'Content-Type': 'application/json;charset=UTF-8' },
		// timeout: 1000,
	});
}

export const instanceFormData = setLoadingInterceptor(createInstanceFormData());

function createInstanceFormData() {
	// axios       create()
	return axios.create({
		baseURL: config.baseUrl,
		headers: { 'Content-Type': 'multipart/form-data' },
		// timeout: 1000,
	});
}
// 'application/x-www-form-urlencoded'
// 'multipart/form-data'
