import { instance, instanceAddLoading } from '@/api/apiConfig/apiConfig.js';
import { webUtils } from '@/utils/jsUtils/webUtils.js';

const prefixUrl = '/code';

export const codeApi = {
	getCommonCodeListApi: params => {
		// params: codeGrpcd
		return instance.get(`${prefixUrl}/getCommonCodeList/${params}`);
	},
	getNationCodeListApi: () => {
		return codeApi.getCommonCodeListApi('MB047');
	},
	getGenderCodeListApi: () => {
		return codeApi.getCommonCodeListApi('AC001');
	},
	getSeasonCodeListApi: () => {
		return codeApi.getCommonCodeListApi('AC002');
	},
};
