import Vue from 'vue';
import Vuex from 'vuex';
import $ from 'jquery';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		pagePath1: '',
		pagePath2: '',
		pagePath3: '',

		isLoading: false, // spinner 

		siteCd: '1',
		selAcntNo: '105914',
		brandNo: '103354',
		prdGroupNo: '1020034',
		dlvTmpltSeq: '109985',

		apiUrl: process.env.VUE_APP_API_URL,
		filePrefix: '/file',
	},
	getters: {
		getServerUrl(state) {
			return state.apiUrl;
		},
		getFilePrefix(state) {
			return state.filePrefix;
		},
	},
	mutations: {
		setIsLoading(state, boolean) {
			state.isLoading = boolean;
		},
	},
	actions: {
		loadingOn({ commit }) {
			commit('setIsLoading', true);
			$('.dim').addClass('active');
		},
		loadingOff({ commit }) {
			commit('setIsLoading', false);
			$('.dim').removeClass('active');
		},
	},
	modules: {},
});
