import { instance, instanceAddLoading, instanceFormData } from '@/api/apiConfig/apiConfig.js';

const prefixUrl = '/product';

export const productApi = {
	selectProductListApi: params => {
		return instanceAddLoading.post(`${prefixUrl}/selectProductList`, params);
	},
	selectProductDetailApi: param => {
		return instanceAddLoading.get(`${prefixUrl}/selectProductDetail/${param}`);
	},
	savePrdDetailPolicyApi: params => {
		return instanceAddLoading.post(`${prefixUrl}/savePrdDetailPolicy`, params);
	},
	getPrdContentsInfoApi: param => {
		return instanceAddLoading.get(`${prefixUrl}/getPrdContentsInfo/${param}`);
	},
	savePrdDetailContentsApi: params => {
		return instanceFormData.post(`${prefixUrl}/savePrdDetailContents`, params);
	},
};
