export let mixinProduct = {
	data() {
		return {
			// 
			paramVo: {
				siteCd: '1',
				schDtTyp: '01',
				strDt: '',
				endDt: '',
				schDtAuto: '',
				ctgrTyp: '01',
				ctgrNo1: '',
				ctgrNo2: '',
				ctgrNo3: '',
				prdStatCd: '',
				prdSelCd: '',
				selAcntNo: this.$store.state.selAcntNo,
				selAcntNm: '',
				mdNo: '',
				brandNo: '',
				brandCd: '',
				brandNm: '',
				prdNmTyp: '01',
				prdNm: '',
				dlvTypCd: '',
				bizTypCd: '',
				prdCdTyp: '01',
				prdCd: '',
				prdCdTextArea: '',
				dispYn: '',
				noDispResnCd: '',
				autoApprYn: '',
				sortCol: '',
				sortCol2: '',
				sortCol3: '',
				sortMode: '',
				appId: '',
				attrCd: '',
				aplBgnDy: '',
				regtr: '',
				brandSearchTyp: '01',

				prdCdArray: [],
				prdStatCdArray: [], // 
				prdSelCdArray: [], //  
				dispYnArray: [], //  
				attrCdArray: [], //  
				gendCdArray: [], //  
				brandNoArray: [], //   
				brandCdArray: [], //   
				brandNmArray: [], //   
				selAcntNoArray: [], //   
				selAcntNmArray: [], //   
				mdNoArray: [], // MD  
				mdNmArray: [], // MD  
				dispChanTypCdArray: [], //  
				stdCtgrNoArray: [], //   

				/***********   **********/
				defaultMdNo: null,
				defaultMdNm: '',
				prdGroupNo: null,
				prdGroupCd: '',
				prdGroupNm: '',
				prdGroupNoArray: [],
				prdGroupCdArray: [],
				prdGroupNmArray: [],
				defaultMdNoArray: [],
				defaultMdNmArray: [],
				createNoArray: [],

				// 
				currentPage: 1,
				currentIndex: 0,
				rowCount: 100,
				prdPageList: [],
			},
		};
	},
	methods: {
		async initProductParam() {
			// this.paramVo
			for (let key in this.paramVo) {
				if (key.indexOf('List') >= 0 || key.indexOf('Array') >= 0) {
					this.paramVo[key].splice(0);
				} else if (key == 'selAcntNo' || key == 'siteCd') {
					continue;
				} else if (key.indexOf('Typ') >= 0) {
					this.paramVo[key] = '01';
				} else {
					this.paramVo[key] = '';
				}
			}
		},

		/**     **/
		setPrdCdArray() {
			// 
			let result = true;
			let prdCdArrayCnt = this.paramVo.prdCdArray.length;
			this.paramVo.prdCdArray = this.$webUtils.toSpaceAndEnterArray(this.paramVo.prdCdTextArea);

			//   - 
			if (prdCdArrayCnt > 0 && this.paramVo.prdCdTyp == '01') {
				for (let item of this.paramVo.prdCdArray) {
					if (item.search(/[0-9]/g) == -1) {
						alert('  .');
						result = false;
						break;
					}
				}
			}
			return result;
		},
		setProductParam() {
			// set 
			if (this.$webUtils.isNotNull(this.paramVo.prdStatCd)) {
				this.paramVo.prdStatCdArray.splice(0);
				this.paramVo.prdStatCdArray.push(this.paramVo.prdStatCd);
			}
			// set 
			if (this.$webUtils.isNotNull(this.paramVo.prdSelCd)) {
				this.paramVo.prdSelCdArray.splice(0);
				this.paramVo.prdSelCdArray.push(this.paramVo.prdSelCd);
			}

			// set /
			if (!this.setPrdCdArray()) return false;

			// set 
			if (this.$webUtils.isNotNull(this.paramVo.ctgrNo3)) {
				this.paramVo.stdCtgrNoArray.splice(0);
				this.paramVo.stdCtgrNoArray.push(this.paramVo.ctgrNo3);
			} else if (this.$webUtils.isNotNull(this.paramVo.ctgrNo2)) {
				if (this.code.ctgr3List && this.code.ctgr3List.length > 0) {
					this.paramVo.stdCtgrNoArray.splice(0);
					let paramStdCtgrNoList = [];
					for (let item of this.code.ctgr3List) {
						paramStdCtgrNoList.push(item.stdCtgrNo);
					}
					this.paramVo.stdCtgrNoArray = paramStdCtgrNoList;
				}
			} else if (this.$webUtils.isNotNull(this.paramVo.ctgrNo1)) {
				if (this.code.ctgr2List && this.code.ctgr2List.length > 0) {
					this.paramVo.stdCtgrNoArray.splice(0);
					let paramStdCtgrNoList = [];
					for (let item of this.code.ctgr2List) {
						paramStdCtgrNoList.push(item.stdCtgrNo);
					}
					this.paramVo.stdCtgrNoArray = paramStdCtgrNoList;
				}
			} else {
				this.paramVo.stdCtgrNoArray.splice(0);
			}

			return this.paramVo;
		},
	},
};
