import Vue from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import jQuery from 'jquery';
import ClipLoader from 'vue-spinner/src/ClipLoader.vue'; // spinner
import '@/utils/jsUtils/cssUtils.js';

import '@/assets/build.css';

// fontawesome 
import '@/fontAwesomeIcon.js';

// vuetify
import Vuetify from 'vuetify';
import 'vuetify/dist/vuetify.css';
// import 'vuetify/dist/';

import { mixinUtils } from '@/utils/jsUtils/mixinUtils.js';
import { webUtils } from '@/utils/jsUtils/webUtils.js';

Vue.use(Vuetify);

window.$ = window.jQuery = jQuery;
// Vue.global
//  mixin 
Vue.mixin(mixinUtils);

// filter 
// eslint-disable-next-line prettier/prettier
import { formatDate, formatDateFull, thousandFormat } from '@/utils/jsUtils/filters';
Vue.filter('formatDate', formatDate);
Vue.filter('formatDateFull', formatDateFull);
Vue.filter('thousandFormat', thousandFormat);
Vue.config.productionTip = false;
Vue.component('ClipLoader', ClipLoader); // spinner component 
const eventBus = new Vue();
Vue.prototype.$eventBus = eventBus;
Vue.prototype.$webUtils = webUtils;

new Vue({
	router,
	store,
	jQuery,
	vuetify: new Vuetify(),
	render: h => h(App),
}).$mount('#app');
